.container {
  height: calc(100vh - 70px - 45px);
  width: 100vw;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

@media only screen and (max-width: 768px) {
  .container {
    height: auto;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: middle;
    justify-content: center;
  }
}
