.modal {
  background-color: var(--main-bg);
  color: var(--text-color);
  border: 1px solid var(--text-color);
}

.modal_header > h5,
.modal_header > button {
  font-size: 0.8rem;
  background-color: var(--main-bg);
  color: var(--text-color);
  border: none;
  padding: 2px;
}

.output {
  font-size: 0.8rem;
  font-weight: 500;
  font-family: "SF-Pro" sans-serif;
  letter-spacing: 0.05rem;
  color: #ffffff;
  margin: 0px;
}
.output_error {
  color: red;
}
