.toolbar {
  height: 70px;
  display: flex;
  justify-content: space-between;
  font-family: SF-Pro;
}

.toolbar_items {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.toolbar_items > .toolbar_button {
  border: 0px;
  background-color: var(--main-bg);
  color: var(--text-color);
  padding: 5px;
  margin: 0px 5px;
}

.toolbar_items > .run_button {
  height: 48px;
  width: 48px;
  border-radius: 50%;
  margin-right: 25px;
  background-color: var(--button-bg);
}

.toolbar_items > .redirect_button {
  text-decoration: none;
  padding: 4px 18px;
  background-color: var(--redirect-button-bg);
  border: 0px;
  border-radius: 30px;
  color: var(--text-color);
  font-size: 16px;
  font-weight: 600px;
  margin-left: auto;
  margin-right: 25px;
}

.toolbar_left > .toolbar_button:last-child {
  margin-right: auto;
}

.toolbar_left > div {
  margin-left: 25px;
  margin-right: 8px;
}

