.modal {
  background-color: var(--button-bg);
  color: var(--text-color);
}

.modal_header > h5,
.modal_header > button {
  font-size: 0.9rem;
}

.setting__option {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.setting__option--data > p {
  font-size: 0.7rem;
  font-weight: 450;
  margin-right: 1.2rem;
}

.setting__option--data > h5 {
  font-size: 0.9rem;
  font-weight: 600;
}
