.header {
  background-color: var(--header-bg);
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 3px solid var(--accent-color);
}

.header > h1 {
  color: var(--text-color);
  text-align: center;
  font-size: 1.6rem;
  font-weight: 600;
  letter-spacing: 2px;
}
